}
.dragBar{
    background-color: #444;
    height: 30px;
    width: 400px;

}

.flip-card {
    perspective: 1000px;
    width: 200px;
    height: 200px;
    cursor: pointer;
    transition: transform 0.5s;
  }
  
  /* Flip card inner container */
  .flip-card-inner {
    width: 100%;
    height: 100%;
    transform-style: preserve-3d;
  }
  
  /* Flip card front and back */
  .flip-card-front,
  .flip-card-back {
    width: 100%;
    height: 100%;
    position: absolute;
    backface-visibility: hidden;
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 24px;
    font-weight: bold;
  }
  
  .flip-card-front {
    background-color: #3498db;
  }
  
  .flip-card-back {
    background-color: #e74c3c;
    
  }